.footer-component {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    background-color: #333;
    color: #9e9e9e;
    padding: 4vw 2vw;
    text-align: left;
}

.footer .title, .footer .bold-subtitle {
    font-weight: bold;
    color: #aaa;
    padding-bottom: 1vw;
}

.footer .title {
    font-size: 1.5vw;
}

.footer .description {
    font-size: 1.2vw;
}

.contact-collumn {
    padding-left: 4vw;
}

.social-media-frame {
    padding-left: 2vw;
}

.icon-media {
    width: 4vw;
    height: 3vw;
    color: #aaa;
}

.footer .line-detail {
    height: 1vw;
    background-color: #3ebb3ef8;
}